*{
  --nav-height: 5rem;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
:root {
  font-size: 10px; /* To decrease the font size */
}

body {
  padding-top: calc(1rem + var(--nav-height));
  background-color: #f4f6f8;
}

/* Utility Classes */
.flex-wrap {
  display: flex;
  flex-wrap: wrap;
}

.gap-1 {
  gap: 1rem;
}
.gap-1_2 {
  gap: .5rem;
}

.align-center {
  display: flex;
  align-items: center;
}
.justify-center {
  display: flex;
  justify-content: center;
}
.justify-ends{
  display: flex;
  justify-content: space-between;
}

.small-img {
  width: 1.3rem;
  aspect-ratio: 1;
  border-radius: 50%;
}
.small-img.smaller {
  width: .8rem;
  height: .8rem;
}

button:hover,
.select:hover {
  cursor: pointer;
  color: rgb(86, 86, 86);
}
/* Utility Classes End */

/* Nav Bar */
nav.nav{
  position: fixed;
  background-color: white;
  inset: 0 0 auto 0;
  height: var(--nav-height);
  box-shadow: 0 0 5px gray;
  padding-left: 5rem;
  z-index: 1;
}

.display-menu {
  flex-direction: column;
  gap: 1rem;
  width: 28rem;
  background-color: white;
  z-index: 2;
  box-shadow: 0 0 5px rgb(225, 225, 225);
  border-radius: 5px;
  padding: 1.5rem 3rem;
}
.display-menu.small {
  width: 15rem;
}
/* Nav Bar End */

/* Open and Close */
.show-cont {
  position: relative;
}

.show, .collapse {
  position: absolute;
  top: calc(100% + .7rem);
  left: 0;
  transition: all .3s;
}

.show {
  clip-path: circle(1);
  transform: translateY(0);
}
.collapse{
  transform: translateY(-30%);
  clip-path: circle(0);
}

/* Open and Close End */

/* Buttons */
button {
  background: none;
  padding: 3px 5px;
  border: 1px solid gray;
  border-radius: 5px;
}

.small-btn {
  width: 7rem;
  text-align: start;
  padding-left: .5rem;
  position: relative;
}
/* Buttons End */

/* Container */
.container {
  padding: 1rem 3rem;
  gap: 2rem;
  justify-content: center;
  align-items: flex-start;
}
/* Container End */

/* Board */
.board,
.board-content{
  flex-direction: column;
  gap: 1rem;
}
.board {
  gap: 2rem;
  margin-bottom: 2rem;
}

.board-title {
  padding: 0 .3rem;
}

.board-heading {
  padding-right: .5rem;
}
/* Board End */

/* Card Design */
.card {
  width: 26rem;
  flex-direction: column;
  gap: .5rem;
  padding: 1rem 1.5rem;
  border-radius: .5rem;
  box-shadow: 0px 0px 3px gray;
  background-color: white;
}

.card .card-id {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  /* padding: 1rem 1.5rem 0 .5rem; */
}

.card .card-id > div {
  color: #808080;
}

.card .card-title {
  font-size: 1.2rem;
  margin-bottom: .5rem;
  font-weight: 600;
}

.card .card-tag-list {
  gap: 1rem;
  align-items: center;
}
.card .card-tag-list .card-tag{
  font-size: .3rem;
  font-weight: 100;
  padding: 2px 5px;
  border: .1px solid #80808060;
  border-radius: 2px;
}

.green, .black {
  position: relative;
  z-index: 0;
}

.green::before, .black::before {
  content: '';
  position: absolute;
  width: 50%;
  aspect-ratio: 1;
  bottom: 0;
  right: 0;
  border-radius: 50%;
}
.green::before {
  background-color: #07f107;
}
.black::before {
  background-color: gray;
}

/* Card End */